import Link from 'next/link'
import React from 'react'

interface ButtonProps {
  title: string,
  link?: string,
  props?: string
}

const Button = ({ title, link, props }: ButtonProps) => {
  return (
    <Link className={` rounded-lg ${props}`} href={link || '#'}>
      {title}
    </Link>
  )
}

export default Button